import { logBody } from '@libs/middlewares';
import middy from '@middy/core';
import _dynamoDB from '@dynamoDB';
import createError from 'http-errors';
import httpErrorHandler from '@middy/http-error-handler';
import httpJsonBodyParser from '@middy/http-json-body-parser';

const placeBid = async (event) => {
	const { id } = event.pathParameters;
	const { amount } = event.body;
	const dynamoDB = await _dynamoDB;

	// const auction = await dynamoDB.getAuctionById(id);
	// if (!auction || auction.highestBid.amount >= amount) {
	// 	throw new createError.Forbidden(`Your bid must be higher than ${auction.highestBid.amount}`);
	// }
	console.log(amount, id);
	const updatedAuction = await dynamoDB.placeBid(id, amount);
	if (!updatedAuction) {
		throw new createError.NotFound(`Auction with id:"${id}" not found`);
	}
	return {
		statusCode: 200,
		body: JSON.stringify({
			updatedAuction,
			id
		})
	};
};

export const main = middy(placeBid).use(logBody()).use(httpJsonBodyParser()).use(httpErrorHandler());
